import React, { useState, useCallback } from 'react'
import styled from '@emotion/styled';

const Title = styled.h2`
  margin-top: 1.5rem;
  color: ${props => props.color || "black"};
  font-size: 2rem;
  font-weight: 600;
  div {
    border-bottom: 3px solid ${props => props.color || "black"};
  }
`
const P = styled.p`
  color: ${props => props.color || "#666666"};
  margin-top:0.7rem;
  margin-bottom: 0.3rem;
  font-size: 0.7rem;
`

const WhiteBox = styled.div`
  background: white;
  width: 250px;
  margin-bottom: 2rem;
`
const BoxList = styled.div`
  display: flex;
  justify-content: flex-start;
  text-align:left;
    img {
      width:24px;
      height:24px;
      margin: 0 6px;
      border-radius: 8px;
    }
    span {
    text-align:left;
    color: #333333;
    font-size: 0.8rem;
    margin: 3px 4px;
    cursor: pointer;
    }
    padding: 3px 0;

    & + & {
      border-top: 1px solid #333333
    }`


const Skils_wrap = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items:center;
  background: white;
  width:300px;
  align-items: center;
  border-radius: 5px;
`

const skillslist = [
  {
    id:1,
    image :"https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSmF_mYe451_AOwhGq8T6OgTVZFZ00oyQdnYgPkwFBSTEBPBqAk&usqp=CAU", 
    text:"HTML / CSS",
    deep: "~~~를 이해하고 있습니다. (대충 하나도 이해 못했다는 뜻)",
    toggle: true
  },
  {
    id:2,
    image :"data:image/png;base64,", 
    text:"JavaScript",
    deep: "자바스크립트에 능숙합니다.",
    toggle: false
  },
  {
    id:3,
    image :"https://cdn.worldvectorlogo.com/logos/react-1.svg", 
    text:"React",
    deep: "엄청 잘합니다. (라고 내년에 말하고 싶습니다.) ",
    toggle: false
  },
  {
    id:4,
    image :"data:image/png;base64,", 
    text:"Node.js",
    deep: "그게 뭔데? 하나도 모릅니다.",
    toggle: false
  },
  {
    id:5,
    image :"data:image/jpeg;base64,", 
    text:"Git",
    deep: "깃? 먹는겁니까",
    toggle: false
  }
]

const Skill = () => {
  
  const toggleSkill = (skill) => {
    skill.toggle = !skill.toggle
    console.log(skill.toggle)
  }
  
  return (
    <Skils_wrap>
      <Title><div>Skills</div></Title>
      <WhiteBox>
        {
        skillslist.map(skill => (
        <BoxList key={skill.id}
        onClick={() =>toggleSkill(skill)}
        ><img src={skill.image} alt=""/>
        <div>
        <span>{skill.text}</span>
        <P>{skill.deep}</P>
        </div>
        </BoxList>
        ))}
      
      </WhiteBox>
    </Skils_wrap>
  )
}

export default Skill;